import { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../css/App.css";

// pw  page
function FindPasswordPage() {
  // email useState update
  const [email, setEmail] = useState("");

  // useNavigate  
  const navigate = useNavigate();

  // input email  email 
  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
  };

  // submit   
  const onSubmitHandler = (event) => {
    //       .
    event.preventDefault();

    // email   alert
    if (email === "") {
      return alert(" .");
    }

    let body = {
      id: localStorage.getItem("ID"),
      email: email,
    };

    // email  email  email pw ,  alert
    fetch(
      "https://server-real.herokuapp.com/findpassword",
      //"http://localhost:4000/findpassword",
      {
        method: "post", // 
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(body),
      }
    )
      .then((response) => response.json())
      .then((res) => {
        if (res["message"]) {
          alert("  .");
        }
        let password = res[0]["password"];
        alert(` ${password}.`);
      })
      .then((res) => {
        navigate("/");
      });
  };

  return (
    <div className="real-background">
      <div className="center-box">
        <form
          style={{ display: "flex", flexDirection: "column" }}
          onSubmit={onSubmitHandler}
        >
          <h2 className="header-text"></h2>
          <input
            type="text"
            value={email}
            onChange={onEmailHandler}
            className="input-box"
          />
          <button type="submit" className="button">
             
          </button>
        </form>
      </div>
    </div>
  );
}

export default FindPasswordPage;
